import { ComponentProps, forwardRef, useState } from 'react';
import TextField from './TextField';

type PasswordTextFieldProps = Omit<ComponentProps<typeof TextField>, 'icon' | 'type'>;

/**
 * Same as `TextField` Component but with special password related features.
 * Shows an Icon at the end of the input to toggle if password is hidden or not.
 * @returns
 */
const PasswordTextField = forwardRef<HTMLInputElement, PasswordTextFieldProps>((props, ref) => {
	const [showPassword, setShowPassword] = useState(false);

	return <TextField {...props} ref={ref} type={showPassword ? 'text' : 'password'} rightButtonLabel={showPassword ? 'Hide' : 'Show'} rightButtonAction={() => setShowPassword(c => !c)} rightButtonClass={''} />;
});

export default PasswordTextField;
