import * as yup from 'yup';
import yupPassword from 'yup-password';
import { useMemo } from 'react';
import { isValidPhoneNumber } from 'react-phone-number-input';

yupPassword(yup);
const regSpecial = /[^A-Za-z0-9]/;
const regLower = /[a-z]/;
const regUpper = /[A-Z]/;
const regNumber = /[0-9]/;
export const allRegs: RegExp[] = [regSpecial, regLower, regUpper, regNumber];

export const useValidation = () => {
	return useMemo(() => {
		return {
			yup,
			schemas: {
				email: yup.string().min(6, 'Username must have at least 8 characters'),
				password: yup.string().min(8, 'Password must have at least 8 characters'),
				
				passwordRepeat: yup
					.string()
					.oneOf([yup.ref('password'), null], 'Passwords does not match')
					.required('Password repeat is required'),
				website: yup.string().url(),
				phoneNumber: yup.string().test({
					name: 'phone-pattern',
					message: 'Not a valid phone number',
					test: (value = '') => value == '' || isValidPhoneNumber(value),
				}),
				mobilePhoneNumber: yup.string().test({
					name: 'mobile-phone-pattern',
					message: 'Not a valid mobile phone number',
					test: (value = '') => value == '' || isValidPhoneNumber(value),
				}),
				employerName: yup.string().required('Employer name is required'),
			},
		};
	}, []);
};
